import { Container, Stack, Typography } from '@mui/material'

import { Page } from '@/shared/components/containers'
import PublicLayout from '@/shared/layout/PublicLayout'

const Privacy = () => (
  <PublicLayout>
    <Page title="Privacidad">
      <Container>
        <Stack justifyContent={'center'} spacing={2} p={5}>
          <Stack spacing={2}>
            <Typography textAlign={'center'} variant="h1">
              Aviso De Privacidad
            </Typography>
            <Typography variant={'body1'}>
              Viabo Pay® (En lo sucesivo Viabo Pay®) Angel Martinez Villarreal #425, Colonia Chepevera. CP. 64030
              Monterrey, N.L. A todos nuestros Clientes: El presente Aviso de Privacidad tiene como fin primordial dar
              cumplimiento a lo establecido por la Ley Federal de Protección de Datos Personales en Posesión de los
              Particulares (en adelante la “Ley”).
            </Typography>
          </Stack>

          <Stack spacing={2}>
            <Typography variant="h3">FINALIDAD DEL TRATAMIENTO DE DATOS</Typography>
            <Typography variant={'body1'}>
              Viabo Pay® solicita los datos personales de sus Clientes con la finalidad de: (i) identificarlos, (ii)
              poder celebrar Convenio de Confidencialidad y Contratos de Prestación de Servicios, (iii) informarles
              sobre el estatus de las obligaciones contraídas a través de los Contratos celebrados, (iv) realizar
              requerimientos de pago, (v) ceder o enajenar los derechos que a su favor se deriven de los Contratos que
              se hubieren celebrado, (vi) otorgarles información relacionada con los Contratos celebrados, (vii)
              ofrecerles los diferentes productos y/o servicios que tiene a su disposición, (viii) consulta y
              verificación de los datos e información otorgada por el Cliente, y (ix) para fines mercadotécnicos,
              publicitarios y de prospección comercial relacionados a Viabo Pay® Igualmente se informa a los Clientes
              que las finalidades que dieron origen a la existencia de la relación jurídica son las enunciadas en los
              incisos (i) y (ii) y aquellas que servirán al mantenimiento y/o cumplimiento de las obligaciones que se
              derivan de la relación jurídica serán las contenidas en los incisos (iii), (iv), (v), (vi), (vii), (viii),
              (ix).
            </Typography>

            <Typography variant={'body1'}>
              Asimismo, a través del presente Aviso de Privacidad, Viabo Pay® garantiza el derecho de sus Clientes para
              manifestar su negativa para el tratamiento de sus datos personales con relación a las finalidades antes
              enunciadas y aquellas que el Cliente considere que no son necesarias para la relación jurídica, para lo
              cual el Cliente deberá informar a Viabo Pay® sobre su negativa al momento en que Viabo Pay® le ponga a
              su disposición el presente Aviso de Privacidad, entendiéndose que la firma del mismo es señal de
              conformidad del Cliente, sin perjuicio de su derecho para revocar el consentimiento que previamente
              hubiere otorgado o bien para ejercer su derecho a oponerse al tratamiento de sus datos en términos de las
              disposiciones aplicables, derechos que en todo caso serán garantizados por Viabo Pay®.
            </Typography>
          </Stack>

          <Stack spacing={2}>
            <Typography variant="h3">OBTENCIÓN Y TRATAMIENTO DE LOS DATOS PERSONALES</Typography>
            <Typography variant={'body1'}>
              Viabo Pay® recaba los datos de sus Clientes (i) de manera personal, (ii) por cualquier otro medio
              permitido por la Ley. Los datos que Viabo Pay® recaba de sus Clientes son tratados a través de medios
              técnicos, administrativos y digitales adecuadas que permiten proteger su información. Los datos de los
              Clientes de Viabo Pay® reciben un tratamiento secreto y confidencial por parte de Viabo Pay®, quien
              adopta para tal efecto las medidas necesarias para evitar su alteración, pérdida, tratamiento o acceso no
              autorizado.
            </Typography>

            <Typography variant={'body1'}>
              Por su parte, el Cliente declara que sus datos son exactos, auténticos y completos y son responsabilidad
              del Cliente que los proporciona, por lo que se libera a Viabo Pay® de cualquier responsabilidad
              relacionada al respecto. Los datos que los Clientes libre y voluntariamente proporcionan a Viabo Pay®,
              incluyen: datos de identificación, datos laborales, datos patrimoniales, actividad a la que se dedican.
            </Typography>
          </Stack>

          <Stack spacing={2}>
            <Typography variant="h3">
              EJERCICIO DE LOS DERECHOS DE ACCESO, RECTIFICACIÓN, CANCELACIÓN U OPOSICIÓN (DERECHOS ARCO)
            </Typography>
            <Typography variant={'body1'}>
              Los Clientes serán responsables de la exactitud, veracidad, autenticidad y vigencia de sus datos. Los
              Clientes de Viabo Pay® tienen el derecho de (i) acceder a los datos personales que les fueron recabados,
              (ii) a rectificar sus datos, en caso de ser estos inexactos o incompletos, (iii) a cancelar sus datos
              cuando a su consideración no son necesarios para alguna de las finalidades señaladas en el presente Aviso
              de Privacidad, y legalmente sea procedente, y (iv) a oponerse al tratamiento de dichos datos, en términos
              de la Ley y las disposiciones aplicables en la materia.
            </Typography>
          </Stack>

          <Stack spacing={2}>
            <Typography variant="h3">REVOCACIÓN DEL CONSENTIMIENTO</Typography>
            <Typography variant={'body1'}>
              En cualquier momento el Cliente podrá informar a Viabo Pay® su determinación de revocar el consentimiento
              que previamente se hubiere otorgado para el tratamiento de los datos personales de acuerdo con las
              finalidades descritas en el presente Aviso de Privacidad, a fin de que Viabo Pay® deje de hacer uso de
              los mismos y siempre y cuando el consentimiento que se pretenda revocar no derive de una relación jurídica
              contractual u operación pactada con Viabo Pay®, que la ley lo permita o que así se hubiere pactado
              expresamente entre Viabo Pay® y el Cliente. En todos los casos la revocación del consentimiento no tendrá
              efectos retroactivos. Los medios y el procedimiento para hacerlo efectivo se mencionan a continuación.
            </Typography>
          </Stack>

          <Stack spacing={2}>
            <Typography variant="h3">LIMITACIÓN DEL USO O DIVULGACIÓN DE SUS DATOS PERSONALES</Typography>
            <Typography variant={'body1'}>
              Cuando el tratamiento de sus datos personales no se refiera a aquellos necesarios para cumplir con una
              obligación legal o contractual (cumplimiento de las obligaciones legales derivadas de la relación jurídica
              existente entre el Cliente y Viabo Pay®), el Cliente tendrá el derecho de solicitar a Viabo Pay® la
              limitación en el uso y/o divulgación de sus datos por medio de la solicitud que Viabo Pay® le
              proporcionará, para que en caso de resultar procedente Viabo Pay® registre los datos personales del
              Cliente en los listados de exclusión dentro del término de 20 (veinte) días naturales contados a partir de
              la presentación de la solicitud respectiva.
            </Typography>
          </Stack>

          <Stack spacing={2}>
            <Typography variant="h3">TRANSFERENCIA DE DATOS PERSONALES POR Viabo Pay®</Typography>
            <Typography variant={'body1'}>
              Viabo Pay® utiliza los datos personales de sus Clientes exclusivamente para los fines señalados en el
              presente Aviso de Privacidad y los conserva por el tiempo que las disposiciones legales señalan. Viabo
              Pay® únicamente comparte y/o transfiere los datos personales de sus Clientes con terceros (i) cuando el
              Cliente contrata otro producto y/o servicio a través de Viabo Pay®, (ii) cuando cedemos y/o enajenamos la
              cartera de créditos, (iii) para hacer requerimientos de pago respecto a las facturas derivadas de la
              prestación de los servicios, (iv) para la prestación de servicios, (v) para consulta y verificación de los
              datos e información otorgada por el Cliente, (vi) cuando así se requiera por disposición legal o por orden
              de autoridad competente debidamente fundada y motivada, y (vii) en los supuestos señalados en el artículo
              37 de la Ley. Fuera de los casos enunciados, el cliente tiene la confianza de que sus datos no serán
              transferidos a terceras personas sin su expreso consentimiento.
            </Typography>
            <Typography variant={'body1'}>
              En todo caso Viabo Pay® garantiza que las finalidades del tratamiento de los datos personales del Cliente
              serán respetadas por los terceros receptores cuando se verifique alguna de las transferencias antes
              enunciadas. Los terceros receptores de los datos personales de los Clientes podrán ser empresas que se
              dediquen o tengan un giro similar al de Viabo Pay®, entidades financieras (nacionales o extranjeras), y
              personas físicas y morales que le presten servicios a Viabo Pay®.
            </Typography>
            <Typography variant={'body1'}>
              Las finalidades de las transferencias antes enunciadas perseguirán (i) el cumplimiento de las
              disposiciones en materia de Datos Personales por parte de Viabo Pay®, (ii) el mantenimiento y/o
              cumplimiento de las obligaciones y derechos contraídos por medio de los Contratos celebrados con los
              Clientes, (iii) protección de los derechos de pago de Viabo Pay®, (iv) la garantía de cualquier derecho
              derivado de los contratos que celebre Viabo Pay®, y (v) protección de los derechos de los Clientes y (vi)
              publicidad y promoción de los productos financieros que Viabo Pay® ofrezca a sus Clientes.
            </Typography>
            <Typography variant={'body1'}>
              En este acto, el Cliente manifiesta su conformidad para que Viabo Pay® pueda llevar a cabo las
              transferencias en los supuestos antes enunciados, a los terceros receptores antes mencionados y por las
              finalidades enunciadas, por lo que firma de enterado y de conformidad el presente Aviso de Privacidad.
            </Typography>
          </Stack>
        </Stack>
      </Container>
    </Page>
  </PublicLayout>
)

export default Privacy
